import React, { useEffect, useRef, useState } from 'react';
import { CCol, CForm, CRow, CFormGroup } from '@coreui/react';
import { RouteComponentProps, useHistory } from 'react-router-dom';
import * as Yup from 'yup';
import config from '../../../config';
import { ERROR, SUCCESS } from '../../../store/callApi/actions';
import { validate } from '../../../utils';
import styles from './ForgotPassword.module.scss';
import ContainerAuth from '../authContainers/Container';
import { HBButton, HBButtonSmall, HBModalConfirm, HBFocusInput } from 'hbBaseClass';
import { useApi } from 'store/callApi';
import { Formik } from 'formik';

const validationSchema = function (values: any) {
  return Yup.object().shape({
    email: Yup.string()
      .min(3, 'Das E-Mail Format ist nicht korrekt.')
      .matches(config.validate.validateEmail(), 'Das E-Mail Format ist nicht korrekt.')
      .required('E-Mail ist erforderlich!'),
  });
};

const ForgotPassword: React.FC<RouteComponentProps> = (props) => {
  const history = useHistory();
  const { callApi } = useApi();

  const query = new URLSearchParams(props.location?.search);
  const email = query.get('email') || '';

  const initialValues = { email };

  const [popup, setPopup] = useState<any>({ show: false, title: '', content: '' });

  /**
   * Close the popup
   */
  const handleClose = () => {
    setPopup({ ...popup, show: false });
  };

  /**
   * Go to Sign In screen
   */
  const gotoSignIn = () => {
    history.push('/sign-in');
  };

  const formRef = useRef<any>();

  /**
   * Validate form on init
   */
  useEffect(() => {
    formRef?.current?.validateForm();
  }, []);

  const resend = (email: string) => {
    callApi(
      {
        method: 'get',
        api: config.rest.resendVerifyEmail(email),
        // msg: config.messages.resendVerifyEmailFailure,
        loading: true,
      },
      ({ text_de }) => {
        setPopup({
          show: true,
          title: 'Information',
          content: text_de || 'Fehler beim erneuten Senden der Verifizierungs-E-Mail.',
          upText: 'Verstanden',
          handleClose: gotoSignIn,
          onClick: gotoSignIn,
        });
      },
    );
  };

  const onSubmit = (values: { email: string }) => {
    callApi(
      {
        method: 'get',
        api: config.rest.forgotPasswordUser(values.email),
        loading: true,
        msg: config.messages.forgotPasswordFailure,
      },
      (response) => {
        const { id, status, text_de } = response;
        if (status === SUCCESS)
          setPopup({
            show: true,
            title: 'Information',
            content:
              'Wir haben Ihnen gerade eine E-Mail gesendet. Bitte folgen Sie den Anweisungen dort, um Ihr Passwort zurückzusetzen.',
            handleClose: gotoSignIn,
          });
        if (status === ERROR) {
          if (id === '1116')
            setPopup({
              show: true,
              title: 'Vergessen fehlgeschlagen',
              content: text_de,
              onClick: () => resend(values.email),
              btnText: 'Erneut senden',
            });
          else
            setPopup({
              show: true,
              title: 'Information',
              content: text_de || 'Ihre E-Mail Adresse konnte nicht gefunden werden. Bitte versuchen Sie es erneut.',
              onClick: handleClose,
            });
        }
      },
    );
  };

  return (
    <div className="petrol">
      <HBModalConfirm
        show={popup.show}
        handleClose={popup.handleClose || handleClose}
        title={popup.title}
        content={popup.content}
        up={popup.btnText || 'Verstanden'}
        upCallback={() => (popup.onClick ? popup.onClick() : gotoSignIn())}
      />
      <ContainerAuth className="FormSignUp-container">
        <Formik
          innerRef={formRef}
          initialValues={initialValues}
          validate={validate(validationSchema)}
          onSubmit={onSubmit}
        >
          {({ values, errors, handleChange, handleBlur, handleSubmit, isValid }) => (
            <CForm onSubmit={handleSubmit}>
              <HBButtonSmall
                type="button"
                color="petrol"
                onClick={() => history.push('/sign-in')}
                className={styles.btnSignUp}
              >
                <i className={`hb-icon-arrow-left ${styles.hbIcon}`} /> Zurück
              </HBButtonSmall>
              <h1 className={styles.cardTitle}>
                Passwort
                <br />
                vergessen?
              </h1>
              <p className={styles.cardContent}>
                Bitte geben Sie Ihre E-Mail Adresse ein und wir senden Ihnen sofort einen Link, damit Sie Ihr Passwort
                zurücksetzen können.
              </p>
              <HBFocusInput
                type="email"
                className="mb-0"
                placeholder="E-Mail"
                autoComplete="email"
                invalid={values.email !== '' && !!errors.email}
                name="email"
                maxLength={50}
                value={values.email}
                onChange={(e: any) => {
                  if (!e.target.value.includes(' ')) handleChange(e);
                }}
                onBlur={handleBlur}
                error={errors?.email}
              />
              <CFormGroup className="m-0">
                <CRow>
                  <CCol xs="12">
                    <HBButton
                      color="violet"
                      children="Passwort zurücksetzen"
                      disabled={!isValid}
                      btnClassName={styles.btnSubmit}
                    />
                  </CCol>
                </CRow>
              </CFormGroup>
            </CForm>
          )}
        </Formik>
      </ContainerAuth>
    </div>
  );
};

export default ForgotPassword;
